import React from "react";
import { Link, useStaticQuery, graphql } from "gatsby";
import Img from 'gatsby-image';

const Hero = () => {
  const data = useStaticQuery(graphql`
    query {
      heroImage: file(relativePath: {eq: "index/hero-main.png"}) {
        childImageSharp {
          fluid(maxHeight: 1080) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return (
    <>
      <section id="hero">
        <div className='row d-flex section-row ml-lg-3' style={{paddingTop: '7rem'}}>
          <div className='col-12 col-sm-12 col-md-6 col-lg-4 mt-md-5 order-0 order-lg-0 hero-text'>
            <div className='text-center text-md-left pl-md-2 pl-lg-5 mx-0 mx-sm-5 mx-md-0 px-0 px-sm-5 px-md-0'>
              <h1 className="d-inline my-lg-5"><span className='text-purple'>  ,</span>    </h1>
              <hr className='d-md-none center my-4' />
              <hr className='d-none d-md-block mt-5' />
              <p className=" my-lg-5" style={{ whiteSpace: 'pre-wrap' }}>
                , ,  <br/>GPON 
                   <br/>   <span className='text-blue-big'>22.99 ./</span>
              </p>
              <div className='text-center text-md-left mt-4 mt-lg-5'>
                <Link to={'/'} className='btn btn-primary green mt-3 mt-md-0 w-75'> </Link>
              </div>
            </div>
          </div>
          <div className='d-none d-md-block col-12 col-md-6 col-lg-8 order-1 order-lg-1'>
            <Img fluid={data.heroImage.childImageSharp.fluid} className='hero-image' alt='    ' />
          </div>
        </div>
      </section>
    </>
  );
}

export default Hero;